import React ,{Component} from "react"
import "./orderdetails.css"
import OrderUp from "../Order-details-up/OrderUp"
import OrderMain from "../Order-details-main/OrderMain"
import OrderDown from "../Order-details-down/OrderDown"

class OrderDetails extends Component{
  state={
    address:[
      {
        name:"",
        id:"909513",
        num:1865149
      },
      {
        name:"",
        id:"2",
        num:1591136
      },
      {
        name:"",
        id:"597211103",
        num:1860734
      }

    ]
  }
  render(){
    const listid=this.state.address.map((item,i)=>(


    <ul key={i} className="goods-id">
      <li>{item.name}</li>
      <li>{item.id}
       <a href="http"></a>
       <a href="http"></a>
       <a href="http"></a>
       <div className="qipao clearfix">
        <a href="http"></a>
        <a href="http"></a>
       </div>
      </li>
      <li>{item.num}</li>
    </ul>

))
    return(
      <div className="orderdetails">
        <OrderUp />


        <div className="main clearfix">
          <div className="maino">
              <h3></h3>
              <h4><a href="http">+</a></h4>
          </div>
          {listid}
          <a className="more-more" href="http"></a>
         </div>
         <OrderMain />
         <OrderDown />


      </div>
    )
  }
}
export default OrderDetails



